html, body {
    height: 100%;
}

#root {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat';
    background-image: url(/tasks-manager/static/media/back.ac3cf131.png);
}

.wrapper-top {
    display: flex;
    flex-direction: column;
}

.tasks-page {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content {
    padding: 10px 10px;
}

.footer {
    background-color: #223c44;
    width: 100%;
    margin: 0;
}

.footer p{
    padding: 0.7rem;
    font-size: 0.9em;
    font-weight: 400;
    letter-spacing: 0.05em;
    margin: 0;
}

header {
    width: 100%;
    height: 50px;
    background-color: #2babd6;
    border-bottom: 2px solid #3298b8;
}

.logo {
    color: white;
    font-size: 1.3em;
    font-weight: 600;
    letter-spacing: 0.05em;
}

.nav-link {
    text-decoration: none;
    color: white;
    font-weight: 500;
    letter-spacing: 0.05em;
}

.nav-link:hover{
    color: rgb(243, 243, 243);
}

.sub_nav {
    padding: 5px 0;
    background-color: #ddf4fc;
    width: 100%;
    border-bottom: 2px solid #c9f2ff;
}

.task_filter_wrap {
    width: 35%;
    margin: 0 20px 0 0;
}

.btn-primary {
    background-color: #2babd6;
    border-color: #3298b8;
}

.btn-primary:hover {
    background-color: #3298b8;
    border-color: #3298b8;
}

.btn-primary:active {
    background-color: #3298b8;
    border-color: #3298b8;
}

.btn-primary:focus {
    background-color: #2babd6;
    border-color: #3298b8;
    box-shadow: none;
}

.alert-info {
    background-color: #ddf4fc;
    color: #223c44;
    font-weight: 500;
    letter-spacing: 0.05em;
    border: 2px solid #d8eef5;
}

.alert h5 {
    margin-bottom: 0;
    letter-spacing: 0.05em;
}
